* {
  box-sizing: border-box;
}
body {
  margin: 0;
  height: 100vh;
}
.App {
  font-family: "Karla";
}

.login {
  position: relative;
}

.login-input {
  position: absolute;
  margin-top: 200px;
  margin-left: 200px;
  display: flex;
  flex-direction: column;
}

.wallpaper {
  position: absolute;
}

input {
  padding: 15px 20px;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  font-size: 1em;
}

input::placeholder {
  font-size: 1.2em;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  font-family: "Karla";
  font-size: 1.2em;
}
button:active {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

h1 {
  color: white;
}
